import React from 'react'
import {Link} from 'react-router-dom'
import {connect} from 'react-redux'
import CartProductsList from '../../../common/features/Cart/CartProductsList'
import productsInCart from '../../../store/rootReducer'
import CartTotal from '../../../common/features/Cart/CartTotal'
import './cart.css'


// const totalPrice = keys(productsInCart).reduce((productsInCart[productId], productsMap[productId].price)=>({
//     return productsInCart[productId] + productsMap[productId].price
// }))

const Cart = ({
    productsInCart,
}) =>
        <div className="cart text-center">
            <CartProductsList productsInCart={productsInCart} />
            <CartTotal productsInCart={productsInCart
            } />
        <Link to="/cart">Show Cart</Link>	
        </div>

const mapStateToProps = (state) => {
    return {
        productsInCart:state.productsInCart
    }
}

export default connect(mapStateToProps,)(Cart)