import React, { Component } from 'react'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
import {connect} from 'react-redux'
import QuantityInput from '../../../common/features/Quintity/QuantityInput'
import './ProductsListItem.css'

class ProductsListItem extends Component {

    static defaultProps = {
        isLiked:false,
    }
    static propTypes = {
        name: PropTypes.string.isRequired,
        description: PropTypes.string,
        type: PropTypes.string,
        capacity: PropTypes.number,
        price: PropTypes.number,
    }
    // static defaultProps = {
    //     description:"No description..."
    // }
    state = {
        productsCount: 1,
    }
    
    onIncrementClick = () => {
        this.setState((prevState)=>({
            productsCount: prevState.productsCount +1
        }))
    }
    onDecrementClick = () => {
        this.setState((prevState)=>({
            productsCount: prevState.productsCount -1
        }))
    }

    renderProductCount() {
        return (
            <QuantityInput
            productsCount={this.state.productsCount}
            onIncrementClick={this.onIncrementClick}
            onDecrementClick={this.onDecrementClick}
            minCount={1}
            />
        )
    }

    onLikeClick = () => {
        if (this.props.isLiked){
            this.props.dispatchDislike(this.props.id)
        } else {
            this.props.dispatchLike(this.props.id)
        }
    }

    render () {
        const {
            id,
            name,
            description = "No description...",
            type,
            capacity,
            price,
            image,
            addProductToCart,
            isLiked,
        } = this.props

        return (
            <div className="products-list-item">
                <div className="product-image">
                    <img src={image} alt={name} />
                </div>
                <button onClick={() => this.onLikeClick()}>
                {isLiked ? <span>&#9829;</span>:<span>&#9825;</span>}
                </button>
                <div className="product-name">
                    <Link to={`products/${id}`}>
                        {name}
                    </Link>
                </div>
                <div className="product-description">{description}</div>
                <div className="product-type">Type: {type}</div>
                <div className="product-capacity">Capacity: {capacity} Gb</div>
                {this.renderProductCount()}
                <div className="product-price">$ {price}</div>
                <button className="btn-add-to-cart" onClick={()=>addProductToCart(id, this.state.productsCount)}>Add to Cart</button>
            </div>
        )
    }
}



