import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import './CartProductListItemExtended.css'
import QuantityInput from '../Quintity/QuantityInput';


const CartProductListItemExtended = ({
    product,
    count,
    deleteProductFromCart,
    setProductQuantity,
    mapStateToProps,
    isLiked,
}) => (
    <div>
        <div className="cart-product-list-item-description">
            <div className="row">
                <div className="col-lg-3">
                    <img src={product.image} alt={product.name}/>
                </div>
                <div className="col-lg-9">
                    <p className="cart-extended-name">
                        <span> {product.name} </span> 
                    </p>
                    <div>{isLiked ? <span>&#9829;</span>:<span>&#9825;</span>}</div>
                    <p className="cart-extended-price">
                         Price for one item: <span className="bold">$ {product.price} </span> 
                    </p>
                    <p className="cart-extended-count">
                         Selected quantity: <span className="bold"> {count} </span> 
                    </p>
                    <p className="cart-extended-sum">
                         Sum for this item: <span className="bold sum-price">$ {(product.price * count)} </span> 
                    </p>
                    <QuantityInput
                        minCount={0}
                        productsCount={count}
                        onIncrementClick={() => setProductQuantity(product.id,count + 1)}
                        onDecrementClick={() => {if (count === 1) {
                            deleteProductFromCart(product.id)
                        } else setProductQuantity(product.id,count - 1) } }
                    />
                <button onClick={() => deleteProductFromCart(product.id)}>Delete item</button>
                
                </div>
            </div>
        </div>
    </div>
)

const mapStateToProps = (state,props) => {
    return {
        isLiked:state.likeProducts[props.product.id]
    }
}

const mapDispatchToProps = (dispatch) => ({
    deleteProductFromCart:(id) => dispatch({
        type:"REMOVE_PRODUCT_FROM_CART",
        id:id,
    }),
    setProductQuantity:(id,count) => dispatch ({
        type:"SET_PRODUCT_QUANTITY",
        id:id,
        count:count,
    })
})

export default connect (
    mapStateToProps,
    mapDispatchToProps,
)(CartProductListItemExtended)

