import productlikereducer from "../common/features/Like/productlikereducer";
import cartReducer from '../common/features/Cart/cartReduer'

const rootReducer = (state={},action) => {
    return {
        likeProducts: productlikereducer(state.likeProducts, action),
        productsInCart: cartReducer(state.productsInCart,action),
    }
}

export default rootReducer