import React from 'react'
import {keys} from 'lodash'
import CartProductsListItem from './CartProductListItem'
import products, { getProductsMap } from './../../../App/Main/Products/products';

const CartProductsList = ({
    productsInCart,
    productsMap = getProductsMap(products),
    CartListComponent = CartProductsListItem,
    deleteProductFromCart,
    setProductQuantity,
}) => {
    return (
            keys(productsInCart).map((productId) => (
                <CartListComponent
                key={productId}
                product = {productsMap[productId]}
                count = {productsInCart[productId]}
                deleteProductFromCart={deleteProductFromCart}
                setProductQuantity={setProductQuantity}
                />
                // <div key={productId}>
                //     <span>
                //         {
                //             productsMap[productId].name
                //         }
                //     </span>:{productsInCart[productId]}
                // </div>
            ))
    )
}

export default CartProductsList